<template>
	<div id="mall-status">
		<public-header title="平台状态"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell>
					<div slot="title">平台状态</div>
					<iswitch :value="close.status" condition-open="1" condition-close="2" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="close.status"></iswitch>
				</van-cell>
				<van-field v-model="close.url" label="平台关闭跳转链接" placeholder="请输入跳转链接" input-align="right" />
				<van-field
					v-model="close.tips"
					label="平台关闭提示语"
					type="textarea"
					placeholder="请输入提示语"
					rows="1"
					input-align="right"
					autosize
				/>
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				close:{
					url: '',
					tips: '',
					status: '1'
				}
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'close',
					url: 'plateform/config/mall/close',
				});
			},
			jsToggleSwitch(res){
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},
			onSubmit() {
				var that = this;
				setData({
					vue: this,
					url: 'plateform/config/mall/close',
					data: that.close
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#mall-status .content .van-field .van-cell__title{
		max-width: 120px;
	}
</style>


